button {
  all: unset;
  position: relative;
  width: 44px;
  height: 44px;
  cursor: pointer;
  margin: 1rem;
  color: #ffffff;
  white-space: nowrap;
}

button:hover {
  background: linear-gradient(145deg, #273846, #202f3b);
  box-shadow: 3px 3px 4px 3px rgba(0, 0, 0, 0.3), -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

button:active {
  background: linear-gradient(145deg, #202f3b, #273846);
  box-shadow: 3px 3px 4px 3px rgba(0, 0, 0, 0.3), -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

button.btn {
  display: inline-block;
  width: auto;
  padding: 0 1rem;
  border-radius: 30px;
}

button.accent {
  color: #243441;
  position: relative;
  transform: scale(0.9);
}

button.accent::before {
  content: ' ';
  position: absolute;
  top: -10px;
  left: -12px;
  bottom: -10px;
  right: -12px;
  background-color: transparent;
  border-radius: 30px;
  box-shadow: inset 3px 3px 6px rgba(0, 0, 0, 0.7), inset -3px -3px 6px rgba(255, 255, 255, 0.2);
}

button.accent:hover {
  background: #0AFFEF;
  box-shadow: 3px 3px 4px 3px rgba(0, 0, 0, 0.3), -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

button.accent:active {
  box-shadow: inset 3px 3px 4px 3px rgba(0, 0, 0, 0.3), inset -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

button.error {
  color: #243441;
  position: relative;
  transform: scale(0.9);
}

button.error::before {
  content: ' ';
  position: absolute;
  top: -10px;
  left: -12px;
  bottom: -10px;
  right: -12px;
  background-color: transparent;
  border-radius: 30px;
  box-shadow: inset 3px 3px 6px rgba(0, 0, 0, 0.7), inset -3px -3px 6px rgba(255, 255, 255, 0.2);
}

button.error:hover {
  background: #dd6f74;
  box-shadow: 3px 3px 4px 3px rgba(0, 0, 0, 0.3), -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

button.error:active {
  box-shadow: inset 3px 3px 4px 3px rgba(0, 0, 0, 0.3), inset -3px -3px 4px 3px rgba(255, 255, 255, 0.1);
}

.disabled {
  pointer-events: none;
  opacity: .5;
}

.text-btn:active {
  text-shadow: 0 0 5px #0AFFEF;
}

.text-btn:hover {
  box-shadow: none;
  background: none;
  color: #0AFFEF;
}

button.btn-round {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.toggle-btn {
  display: inline-block;
  margin: .5rem;
  cursor: pointer;
  position: relative;
  transition: .1s ease-in-out;
}

.toggle-btn::before {
  content: ' ';
  transition: .1s ease-in-out;
  display: block;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  border: 7px solid #243441;
  box-shadow: 2px 0 5px 3px rgba(0, 0, 0, 0.5);
}

.toggle-btn-on {
  display: inline-block;
  margin: .5rem;
  cursor: pointer;
  position: relative;
  transition: .1s ease-in-out;
}

.toggle-btn-on::before {
  content: ' ';
  transition: .1s ease-in-out;
  display: block;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 51%;
  border-radius: 50%;
  background-color: transparent;
  border: 7px solid #243441;
  box-shadow: 2px 0 5px 3px rgba(0, 0, 0, 0.5);
}

input[type="range"].slider-accent {
  -webkit-appearance: none;
  width: 200px;
  height: 10px;
  border-radius: 4px;
  box-shadow: inset 2px 2px 3px 3px rgba(0, 0, 0, 0.4), inset -2px -2px 3px 3px rgba(255, 255, 255, 0.1);
  outline: none;
  border: none;
  position: relative;
  cursor: pointer;
}

input[type="range"].slider-accent::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #0AFFEF;
  position: relative;
  top: -181%;
  cursor: pointer;
  border: 8px solid #243441;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

input[type="range"].slider-accent::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
  border-radius: 5px;
  box-shadow: inset 2px 2px 3px 3px rgba(0, 0, 0, 0.4), inset -2px -2px 3px 3px rgba(255, 255, 255, 0.1);
}

input[type="range"].slider-accent::-moz-range-progress {
  background-color: #0AFFEF;
}

input[type="range"].slider-error {
  -webkit-appearance: none;
  width: 200px;
  height: 10px;
  border-radius: 4px;
  box-shadow: inset 2px 2px 3px 3px rgba(0, 0, 0, 0.4), inset -2px -2px 3px 3px rgba(255, 255, 255, 0.1);
  outline: none;
  border: none;
  position: relative;
  cursor: pointer;
}

input[type="range"].slider-error::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #dd6f74;
  position: relative;
  top: -181%;
  cursor: pointer;
  border: 8px solid #243441;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

input[type="range"].slider-error::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
  border-radius: 5px;
  box-shadow: inset 2px 2px 3px 3px rgba(0, 0, 0, 0.4), inset -2px -2px 3px 3px rgba(255, 255, 255, 0.1);
}

input[type="range"].slider-error::-moz-range-progress {
  background-color: #dd6f74;
}

.slider-progress-accent {
  position: absolute;
  width: 75px;
  height: 4px;
  top: 13px;
  left: 8px;
  background-color: #0AFFEF;
  border-radius: 10px;
  background-image: url(data:image/png;base64,);
  background-size: 12%;
  background-attachment: fixed;
  background-repeat: repeat;
  background-blend-mode: hue;
  transition: none;
}

.slider-progress-error {
  position: absolute;
  width: 75px;
  height: 4px;
  top: 13px;
  left: 8px;
  background-color: #dd6f74;
  border-radius: 10px;
  background-image: url(data:image/png;base64,);
  background-size: 10%;
  background-attachment: fixed;
  background-repeat: repeat;
  background-blend-mode: darken;
  transition: none;
}

