  padding: 1rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  transition: 0.5s;
}

.pathAlgo-options {
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  transition: 0.2s ease-in-out;
}

.pathAlgo-options > div {
  padding: 1rem;
  cursor: pointer;
}

.pathAlgo-options > div:hover {
  background-color: rgba(0, 0, 0, 0.2);
  color: #0AFFEF;
}

.search-field {
  width: 98%;
  height: 390px;
  overflow: hidden;
  position: relative;
  margin: auto;
  top: 1rem;
}

.search-field > div {
  width: 1250px;
  height: 100%;
  margin: auto;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[50];
      grid-template-columns: repeat(50, 1fr);
  -ms-grid-rows: (1fr)[15];
      grid-template-rows: repeat(15, 1fr);
  grid-gap: 0;
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.2), transparent, rgba(255, 255, 255, 0.1), transparent, rgba(0, 0, 0, 0.2));
  border-radius: 10px;
  overflow: hidden;
}

.node {
  opacity: 0.1;
  border: 1px solid white;
}

.start {
  background-color: yellowgreen;
  opacity: 1;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  border: 2px solid white;
  transition: 0;
  transform: scale(1.3);
  animation: SCALE 1s ease;
}

.finish {
  background-color: crimson;
  opacity: 1;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  border: 2px solid white;
  transition: 0;
  transform: scale(1.3);
  animation: SCALE 1s ease;
}

@keyframes SCALE {
  from {
    transition: ease-in-out;
    transform: scale(0.5);
  }
  to {
    transition: ease-in-out;
    transform: scale(1.3);
  }
}

@keyframes SCALE2 {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.obstacle {
  background-color: rgba(255, 255, 255, 0.8);
  animation: weight 0.4s ease-in-out;
}

.path {
  background-color: cyan;
  opacity: 1;
  border: none;
  transition: 0.3s ease;
  animation: path 1.5s;
}

@keyframes path {
  from {
    background-color: red;
  }
  to {
    background-color: cyan;
  }
}

.visited {
  transition: 0.3s ease-in;
  background: radial-gradient(orange, yellow);
  opacity: 1;
  border: none;
  animation: SCALE2;
}

.pathFinding-panel {
  position: absolute;
  bottom: 2rem;
  height: 50px;
  width: 90vw;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}

.pathFinding-panel > div {
  width: 1000px;
  height: 100%;
  margin: auto;
  display: flex;
  position: relative;
  justify-content: center;
}

.weight {
  opacity: 0.5;
  border: none;
  background: url(/static/media/weight.bb6aaecd.png);
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: cyan;
  animation: weight 0.4s ease-in-out;
}

@keyframes weight {
  0% {
    background-size: 0%;
  }
  80% {
    background-size: 60%;
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .pathFinding-panel {
    overflow-x: scroll;
    width: 80vw;
    bottom: 5rem;
  }
  .pathFinding-panel > div {
    margin: 0;
    justify-content: flex-start;
    width: auto;
  }
  .search-field {
    overflow-x: scroll;
  }
  .linearSearch > div,
  .binarySearch > div {
    width: 1000px;
  }
  .rgba2 {
    overflow-x: scroll;
  }
  .searchingVisualizerPanel > div {
    width: 800px;
  }
  .searchVisualizerContainer {
    overflow-y: scroll;
  }
  .sorting-panel {
    flex: none;
    justify-content: left;
    overflow-x: scroll;
    width: 100%;
  }
  .bars-container {
    justify-content: center;
    flex-wrap: nowrap;
    width: 100%;
    overflow-x: scroll;
  }
  .bars {
    width: 3px;
    flex: none;
  }
}
